import React from 'react';

const axios = require('axios');
var dateFormat = require('dateformat');

export function Orders({ data, isLocal, clickType, onlinePay, driverinfo }) {
  const buttonstylefocus = {
    cursor: 'pointer',
    textAlign: 'center',
    display: 'inline-table',
    padding: '13px 20px',
    borderRadius: '10px',
    fontSize: '16px',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    fontWeight: '700',
    border: '1px solid #f4511e',
    color: '#fff',
    background: '#f4511e',
    boxShadow: '0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 20px 29px 0 rgba(0, 0, 0, 0.19)',
    wordWrap: 'break-word'
  };
  const buttonstyle = {
    cursor: 'pointer',
    textAlign: 'center',
    display: 'inline-table',
    padding: '13px 20px',
    borderRadius: '10px',
    color: '#f4511e',
    fontSize: '16px',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    border: '1px solid #f4511e',
    fontWeight: '700',
    textDecoration: 'none',
    background: 'transparent',
    wordWrap: 'break-word'
  };
  const styleForm = {
    paddingLeft: '10px',
    paddingBottom: '5px'
  }
  let orders = []
  let i = 0;
  for (i = 0; i < data.length; i++) {
    let booktime = dateFormat(data[i].BookDate, "dddd, mmm dS, yyyy h:MM:ss TT", true);
    let amt1 = data[i].NetTotal
    let id1 = data[i].id
    let id = data[i].BookingId
    let otp = data[i].otp
    let driver1 = data[i]
    let pickup = data[i].isPickup
    let dropotp = data[i].recotp
    let usermobno = data[i].UserMobileno
    let drivermobno = data[i].Drivermobile
    const viewpickup = <div>View Pickup&nbsp;OTP</div>
    const viewdrop = <div>View Drop&nbsp;OTP</div>

    orders.push(
      <div key={i} className="card card-default">
        <div style={{ marginBottom: '10px' }}>
          <div className="callout callout-danger">
            <div className="row">
              <div className="col-9">
                <h3>Order #{data[i].BookingId}</h3>
              </div>
              <div className="col-3" >
                {data[i].Refno === '' ? <button className="btn btn-sm btn-danger" onClick={() => onlinePay({ driver1 })} disabled>Unpaid</button> : <button className="btn btn-sm btn-success" disabled readOnly>Paid</button>}
              </div>

              <div className="col-12 mb-2">
                <p>Placed on {booktime}</p>
              </div>

              <div className="col-12">
                <div style={styleForm}>
                  <label className="select2text">From</label>
                  <div className="input-group">
                    <span className="input-group-text text-success"><i className="fa fa-dot-circle-o"></i></span>
                    <div className="input-group-prepend">
                    </div>
                    <input type="text" className="form-control" disabled value={data[i].FromAddress} />
                  </div>
                </div>
              </div>

              <div className="col-12">
                <div style={styleForm}>
                  <label className="select2text">To</label>
                  <div className="input-group">
                    <span className="input-group-text text-danger"><i className="fa fa-dot-circle-o"></i></span>
                    <div className="input-group-prepend">
                    </div>
                    <input type="text" className="form-control" disabled value={data[i].toaddress} />
                  </div>
                </div>
              </div>

              <div className="col-7">
                <div style={styleForm}>
                  <label className="select2text">Courier Type</label>
                  <div className="input-group mb-3">
                    <span className="input-group-text text-primary"><i className="fa fa-envelope-o"></i></span>
                    <div className="input-group-prepend">
                    </div>
                    <input type="text" className="form-control" disabled value={data[i].Type} />
                  </div>
                </div>
              </div>
              <div className="col-5 mb-2">
                <div style={styleForm}>
                  <label className="select2text">Amount</label>
                  <div className="input-group mb-3">
                    <span className="input-group-text bg-danger"><i className="fa fa-inr"></i></span>
                    <div className="input-group-prepend">
                    </div>
                    <input type="text" className="form-control" disabled value={data[i].NetTotal} />
                  </div>
                </div>
              </div>

              <div className="col-6 col-md-3 mb-2 text-center">
                <a href="#" style={isLocal ? buttonstylefocus : buttonstyle} className="orange-back-link" data-toggle="modal" data-target="#myModal3" onClick={() => onlinePay({ driver1 })}>{data[i].isPickup == 0 ? viewpickup : viewdrop}</a>
              </div>
              <div className="col-6 col-md-3 text-center" disabled={data[i].isPickup == 0 && data[i].Refno === '' ? false : true}>
                <a href="#" style={isLocal ? buttonstylefocus : buttonstyle} className="orange-back-link" data-toggle="modal" data-target="#myModal2" onClick={() => onlinePay({ driver1 })}>Setup Payment</a>
              </div>
              <div className="col-6 col-md-3 text-center" disabled={data[i].isPickup == 0 ? false : true}>
                <a href="#" style={isLocal ? buttonstylefocus : buttonstyle} className="orange-back-link" onClick={() => clickType('cancelbooking', { id }, { usermobno })}>Cancel Booking</a>
              </div>
              <div className="col-6 col-md-3 text-center" disabled={drivermobno === null ? true : false}>
                <a href="#" style={isLocal ? buttonstylefocus : buttonstyle} className="orange-back-link" data-toggle="modal" data-target="#driverModal" onClick={() => driverinfo({ driver1 })} >Driver Info</a>
              </div>

            </div>

          </div>

        </div>
      </div>
    );
  }
  return (
    <div>
      {orders}
    </div>
  );
}

export const MemoizedOrder = React.memo(Orders);